import React, { Fragment } from "react";
import { RiskItem } from "../types";
import MainButton from "./MainButton";

const data: RiskItem[] = require("../data/vulnerabilities.json");

function getInitialState() {
    return data.slice(0, 20).map((value, index) => ({
        index: index,
        data: value,
        visited: false,
    }));
}

class MainGrid extends React.Component<any, any> {
    state = {
        buttons: getInitialState(),
    };

    visitButton = index => {
        this.setState({});
    };

    render() {
        const { buttons } = this.state;
        return (
            <div className="main-grid">
                {buttons.map((button, index) => (
                    <div key={index} className="array-item-wrapper">
                        <MainButton {...button} />
                        <span className="array-comma">,</span>
                    </div>
                ))}
            </div>
        );
    }
}

export default MainGrid;
